<template>
  <div class="addAddress mescroll">
    <div class="list"> 
      <div class="item item-center flex">
        <div class="label">手机号</div>
        <div class="value flex-2">
          <van-field v-model="telno" placeholder="请输入手机号" type="tel" clearable />
        </div>
      </div>  
    </div>
    <div class="btnWrap">
      <div class="btn" @click="save">查找账号资料</div>
    </div> 
  </div>
</template>
<script>
import { Switch, Field, Popup, Area } from "vant";
import {findFriendJust } from "@/api"; 
export default {
  components: {
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area
  },
  data() {
    return { 
      telno: "",
      clickFlag: true,
      userCode: ""
    };
  },
  methods: {  
    loadData() {  
    },
    save(){
      if (!this.clickFlag) {
        return;
      } 
      if (!this.telno) {
        this.$toast("请输入好友手机号");
        return;
      } 
      this.clickFlag = false;
      console.log(111);
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      let that = this;
        findFriendJust({
              usermobile: this.telno 
            }).then(res => {
              loading.clear();
              that.clickFlag = true;
              if (res.code == 0) { 
                //that.$toast(res.msg);  
                 this.$router.push({
                    name: "addUser2",
                    query: { 
                        id: res.data.userCode
                    },
                });
                 //window.location.href = this.$config.host2 + "/wx/#/pay?type=2&userCode="  + res.data.userCode;
               
              } else {
                this.$router.push({
                    name: "addUser3",
                    query: { 
                      telno : this.telno
                    },
                });
              }
            }).catch(() => {
          // on cancel
          that.clickFlag = true;
        });
    }
  },
  mounted() {
    document.title =  "查找账号资料"; 
    this.loadData(); 
  }
};
</script>
<style lang="scss" scoped>
.addAddress {
  background: #fff;
  .list {
    padding: 0 15px;
    .flex {
      display: flex;
    }
    .item {
      padding: 15px 0;
      border-bottom: 1pt solid #efefef;
      .flex-2 {
        flex: 2;
      }

      .label {
        font-size: 15px;
        line-height: 30px;
        font-weight: bold;
        margin-right: 15px;
        width: 60px;
      }
      .value {
        input {
          height: 30px;
          font-size: 15px;
          color: #666;
          width: 100%;
        }
        textarea {
          width: 100%;
          height: 30px;
          font-size: 15px;
          color: #666;
          margin-top: 7px;
        }
      }

      .value {
        .prompt {
          color: #999;
          font-size: 12px;
          line-height: 20px;
          span {
            color: $base-color;
          }
        }
      }
    }
    .area {
      .value {
        font-size: 15px;
        color: #666;
      }
      img {
        width: 5px;
      }
    }
    .item-center {
      align-items: center;
    }
    .item-bottom {
      .label {
        width: 100px;
        line-height: 37px;
      }
      .value {
        text-align: right;
        margin: 3.5px 0 0;
      }
    }
  }
  .btnWrap {
    background: #fff;
    margin: 20px 0 0;
    width: calc(100% - 30px);
    padding: 0 15px 10px;
    .btn {
      background: $base-color;
      color: #fff;
      font-size: 18px;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
      border-radius: 5px;
    }
  }
}
</style>
<style lang="scss">
.addAddress {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell {
    padding: 5px 0;
  }
}
</style>
